<template>
  <!-- Modal -->
  <div
    class="modal fade"
    id="exampleModal"
    tabindex="-1"
    aria-labelledby="exampleModalLabel"
    aria-hidden="true"
    ref="modal"
  >
    <div class="modal-dialog modal-xl">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalLabel"></h5>
        </div>
        <div class="modal-body">
          <!-- Form  -->
          <div>
            <el-form ref="tagsForm3" :model="addTagsForm" :rules="rules" class="demo-ruleForm">
              <!--  -->
              <el-form-item label="" prop="title" class="ms-2 el-form-style">
                <el-input class="tagName_style" v-model="addTagsForm.title"></el-input>
              </el-form-item>
              <!--  -->
              <el-form-item label="" prop="content" class="ms-2 el-form-style">
                <el-input class="contentName_style" v-model="addTagsForm.content"></el-input>
              </el-form-item>
              <!--  -->
              <el-form-item label="" prop="remark" class="ms-2 el-form-style">
                <el-input class="contentName_style" v-model="addTagsForm.remark"></el-input>
              </el-form-item>
              <!--  -->
              <el-form-item class="ms-3 el-form-style" label="" prop="domain">
                <el-radio-group
                  v-model="addTagsForm.domain"
                  size="small"
                  class="radio-group"
                  @change="changeStatus($event)"
                >
                  <el-radio label="" class="radio-no"></el-radio>
                  <el-radio label=""></el-radio>
                </el-radio-group>
              </el-form-item>
              <!-- - -->
              <el-form-item
                v-if="addTagsForm.domain === ''"
                label="https://"
                class="ms-3"
                prop="domain_yes"
              >
                <div class="d-flex w-75">
                  <el-input
                    placeholder="www.google.com"
                    class="domain_style me-3"
                    v-model="addTagsForm.domain_name"
                  ></el-input>
                  <el-button
                    type="primary"
                    :disabled="addTagsForm.domain_name === ''"
                    color="#626aef"
                    @click="confirmDomain()"
                    ></el-button
                  >
                </div>
              </el-form-item>
            </el-form>
          </div>
        </div>
        <div class="modal-footer">
          <button type="button" class="btn btn-secondary" @click.prevent="resetForm()"></button>
          <button type="button" class="btn btn-primary" @click="doRegister()"></button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Modal from 'bootstrap/js/dist/modal';
import { ElMessage } from 'element-plus';
// import isDark from '~/';

export default {
  data() {
    return {
      // 
      addTagsForm: {
        title: '', // 
        content: '', // 
        remark: '', // ()
        domain: '', // (or)
        domain_yes: '', // (or)
        domain_name: '', // 
      },
      // 
      rules: {
        title: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        content: [
          {
            required: true,
            message: '',
            trigger: 'blur',
          },
        ],
        remark: [
          {
            required: false,
            message: '',
            trigger: 'change',
          },
        ],
        domain: [
          {
            required: true,
            message: '',
            trigger: 'change',
          },
        ],
      },
    };
  },
  methods: {
    showModal() {
      this.modal.show();
    },
    // 
    hideModal() {
      this.modal.hide();
    },
    // 
    resetForm() {
      this.$refs.tagsForm3.resetFields();
    },
    // 
    changeStatus(item) {
      console.log(item);
      if (item === '') {
        this.addTagsForm.domain = ''; // 
      } else if (item === '') {
        this.addTagsForm.domain = ''; // 
      }
    },
    // 
    confirmDomain() {
      ElMessage({ showClose: true, message: '!', type: 'success' });
    },
    // 
    doRegister() {
      const testapi = `${process.env.VUE_APP_TESTAPI}`;
      // 
      this.$refs.tagsForm3.validate((valid) => {
        // 
        if (!valid) {
          console.log(',');
          return false;
        }
        // truefalse
        console.log('');
        // 
        // 
        // ....
        // axios
        this.$http
          .post(`${testapi}/backend/members/tags_add`, this.addTagsForm)
          .then((response) => {
            if (response.data.code === 200) {
              this.$swal.fire('!', `'${response.data.msg}`, 'success');
              this.modal.hide();
              this.$router.push('tags');

              console.log(response.data);
            } else {
              this.$swal.fire('', `${response.data.msg}`, 'error');
              console.log(response.data);
            }
          })
          .catch((error) => {
            console.log(error);
            console.log('');
          });

        return true;
      });
      //   resetForm(); // 
    },
  },
  mounted() {
    this.modal = new Modal(this.$refs.modal);
    // this.modal.show();
  },
};
</script>

