<template>
  <div class="text-end">
    <button
      class="btn btn-primary btn-sm me-2 mb-2"
      type="button"
      @click="$refs.historyGroupListModal.showModal"
    >
      
    </button>
    <button class="btn btn-primary btn-sm me-2 mb-2" type="button"></button>
    <button class="btn btn-primary btn-sm me-2 mb-2" type="button"></button>
    <button
      class="btn btn-primary btn-sm me-2 mb-2"
      type="button"
      @click="$refs.addGroupListModal.showModal"
    >
      
    </button>
  </div>
  <!-- body -->
  <el-table :data="groupDetail" border stripe style="width: 100%">
    <el-table-column prop="group_name" label="" align="center" />
    <el-table-column prop="currency" label="" align="center"> </el-table-column>
    <el-table-column sortable prop="status" label="" align="center">
      <template v-slot="{ row }">
        <el-tag
          :type="
            row.status === ''
              ? 'success'
              : 'danger' && row.status === ''
              ? 'warning'
              : 'danger' && row.status === '0'
              ? 'warning'
              : 'danger' && row.status === '1'
              ? 'warning'
              : 'danger' && row.status === '2'
              ? 'warning'
              : 'danger'
          "
        >
          {{ row.status }}
        </el-tag>
      </template>
    </el-table-column>
    <el-table-column prop="reserve_level" label="" align="center" />
    <el-table-column prop="currency" label="" align="center" />
    <el-table-column prop="currency" label="" align="center">
      <el-table-column prop="deposit_bank" label="" align="center" />
      <el-table-column prop="deposit_max" label="" align="center" />
    </el-table-column>
    <el-table-column prop="deposit_max" label="" align="center">
      <el-table-column prop="withdraw_max" label="" align="center" />
      <el-table-column prop="deposit_max" label="(%)" align="center" />
      <el-table-column prop="deposit_max" label="" align="center" />
    </el-table-column>
    <el-table-column prop="createtime" label="" align="center" />
    <el-table-column label="" align="center" />
    <el-table-column label="" align="center">
      <template #default="scope">
        <el-button size="small" type="warning" @click="handleEdit(scope.$index, scope.row)"
          >Detail</el-button
        >
      </template>
    </el-table-column>
  </el-table>

  <!--  -->
  <div class="titleStyle">
    <div class="block">
      <el-pagination
        :small="small"
        background
        @current-change="handleCurrentChange"
        @size-change="sizechange"
        :current-page="currentPage"
        :page-sizes="pagesizes"
        :page-size="pagesize"
        layout="prev, pager, next, sizes"
        :total="total"
        style="margin: 10px 0px"
      ></el-pagination>
    </div>

    <!--  -->
    <div class="selectStyle">
      <span class=""> </span>
      <el-select size="small" v-model="value" placeholder="..." @change="filterItem(value)">
        <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item">
        </el-option>
      </el-select>
    </div>
  </div>
  <!--   -->
  <GroupListHistory ref="historyGroupListModal"></GroupListHistory>
  <!--  -->
  <GroupListAdd ref="addGroupListModal"></GroupListAdd>
  <!--   -->
  <GroupListDetail ref="GroupListDetailModal"></GroupListDetail>
</template>

<script>
import _ from 'lodash';
import GroupListAdd from '../components/OpenModal/GroupList_Add.vue';
import GroupListHistory from '../components/OpenModal/GroupList_History.vue';
import GroupListDetail from '../components/OpenModal/GroupList_Detail.vue';

export default {
  data() {
    return {
      groupDetail: [],
      small: true, // ""
      value: '', // 
      options: [
        // 
        {
          value: 'Option1',
          label: 'ID',
        },
        {
          value: 'Option2',
          label: 'ID',
        },
        {
          value: 'Option3',
          label: '',
        },
      ],
      total: 60, // 
      currentPage: 1, // 
      pagesize: 10, // 
      pagesizes: [10, 20, 30, 50],
      search: '',

      orderType: 'DESC', // ID(DESC , ASC )
      orderBy: '', // ( ... money )
    };
  },
  components: {
    GroupListAdd,
    GroupListHistory,
    GroupListDetail,
  },
  methods: {
    getGroupDetail(limit = 20, skip = 0) {
      const testapi = `${process.env.VUE_APP_TESTAPI}`;
      this.isLoading = true;
      this.$http
        .get(
          `${testapi}/backend/members/group_list?skip=${skip}&limit=${limit}&order=${this.orderType}&orderType`,
        )
        .then((res) => {
          this.isLoading = false;
          console.log(res.data);
          if (res.data.code === 200) {
            //   console.log(res.data.message);
            this.groupDetail = res.data.data.list;
            // forEach groupDetailitem
            _.forEach(this.groupDetail, (item, key) => {
              console.log(item, key);
              if (item.status === 'disable') {
                // 0
                this.groupDetail[key].status = '';
              } else if (item.status === 'enable') {
                // 1
                this.groupDetail[key].status = '';
              }
            });

            // console.log(this.newMember);
            // console.log(this.newMemberList);
          }
          // console.log(res.data);
        });
    },
    // 
    handleCurrentChange(skip) {
      this.currentPage = skip; //   skip
      if (skip <= 1) {
        //  1
        this.getGroupDetail(this.pagesize);
      } else if (skip > 1) {
        // 2.3.4.5.....
        this.getGroupDetail(this.pagesize, this.pagesize * (skip - 1));
      }
    },
    // 
    sizechange(limit) {
      this.pagesize = limit;
      this.getGroupDetail(limit);
      this.currentPage = 1;
    },
    // 
    filterItem(value) {
      if (value.value === 'Option1') {
        this.orderType = 'ASC';
        this.getGroupDetail();
        console.log('1');
      } else if (value.value === 'Option2') {
        this.orderType = 'DESC';
        this.getGroupDetail();
        console.log('2');
      } else {
        // this.orderType = 'DESC';
        this.orderBy = 'id';
        this.getGroupDetail();
        console.log('3');
      }
    },
    handleEdit(index, row) {
      console.log(index, row);
    },
  },
  created() {
    this.getGroupDetail();

    // this.getAddValue()
  },
};
</script>

