<template>
  <!-- body -->
  <el-table :data="failList" border stripe style="width: 100%">
    <el-table-column prop="id" label="ID" />
    <el-table-column prop="account" label="" />
    <el-table-column prop="name" label=""> </el-table-column>
    <el-table-column label="" />
    <el-table-column prop="status" label="" />
    <el-table-column prop="loginTime" label="" />
    <el-table-column prop="country" sortable label="" />

    <el-table-column label="" />
    <el-table-column prop="ip" label="IP" />
    <el-table-column label="" />
    <el-table-column prop="terrace" label="/" />
    <el-table-column label=""> </el-table-column>
  </el-table>

  <!--  -->
  <div class="block">
    <el-pagination
      background
      @current-change="handleCurrentChange"
      @size-change="sizechange"
      :current-page="currentPage"
      :page-sizes="pagesizes"
      :page-size="pagesize"
      layout="prev, pager, next, sizes"
      :total="total"
      style="margin: 10px 0px"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  data() {
    return {
      failList: [],

      total: 60, // 
      currentPage: 1, // 
      pagesize: 10, // 
      pagesizes: [10, 20, 30, 50],
      search: '',
    };
  },
  methods: {
    getFailList() {
      const testapi = `${process.env.VUE_APP_TESTAPI}`;
      this.isLoading = true;
      this.$http
        .get(`${testapi}/backend/members/login_Log?skip=0&limit=20&order=DESC&orderType`)
        .then((res) => {
          this.isLoading = false;
          console.log(res.data);
          if (res.data.code === 200) {
            //   console.log(res.data.message);
            this.failList = res.data.data.list;
            // console.log(this.newMember);
            // console.log(this.newMemberList);
          }
          // console.log(res.data);
        });
    },
    // 
    handleCurrentChange(skip) {
      this.currentPage = skip; //   skip
      if (skip <= 1) {
        //  1
        this.getFailList(this.pagesize);
      } else if (skip > 1) {
        // 2.3.4.5.....
        this.getFailList(this.pagesize, this.pagesize * (skip - 1));
      }
    },
    // 
    sizechange(limit) {
      this.pagesize = limit;
      this.getFailList(limit);
      this.currentPage = 1;
    },
  },
  created() {
    this.getFailList();
    // this.getAddValue()
  },
};
</script>

<style lang="scss" scoped></style>
